.ui-header {
  position: relative;

  @media screen and (max-width: $BP_LG) {
    text-align: center;
  }
  &--wrapper {
    background-color: $GRAY_LIGHT;
    padding: 2rem 0 4rem 0;
  }
  &--title {
    font-size: 7.2rem;
  }
  &--subtitle {
    font-size: 1.8rem;
    letter-spacing: 0.05rem;
    line-height: 2.6rem;
  }
  &--nav {
    position: absolute;
    bottom: 0;
    right: 0;

    li,
    a {
      font-size: 2rem;
    }
  }
}
